html {
  font-family: "Roboto", sans-serif;
  --main-color: #060b35;
  --secondary-color: #616070;
  --white-color: #fff;
  --blue-color: #00acdc;
  --grey-color: #e5e5e5;
  --yellow-color: #ffde6a;
}

body,
p,
h2 {
  margin: 0;
  line-height: 0;
}

a {
  text-decoration: none;
  color: inherit;
  padding: 0;
  margin: 0;
}
